      animation: moveClouds3 11s linear infinite;
      left: 65%; } } }

@keyframes moveClouds1 {
  0% {
    left: 0%; }
  100% {
    left: 100%; } }
@keyframes moveClouds2 {
  0% {
    left: 100%; }
  100% {
    left: 0%; } }
@keyframes moveClouds3 {
  0% {
    left: 100%; }
  100% {
    left: 0%; } }

.slick-slide {
  &:focus {
    outline: none; } }

.slick-slide.slick-active {
  .main-slider__block {
    .car-rent--first {
      opacity: 1;
      transform: translateX(0); }
    .car-rent--second {
      opacity: 1;
      transform: translateX(-249px) translateY(0px); }
    .car-rent--third {
      opacity: 1;
      transform: translateX(0); } }
  .main-slider__block {
    img.img-1 {
      opacity: 1;
      transform: translateX(0); }
    img.img-2 {
      opacity: 1;
      transform: translateY(0); }
    img.img-3 {
      opacity: 1;
      transform: translateX(0); } } }

.popup {
  position: relative;
  background: $color-white;
  padding: 30px 23px 70px 23px;
  width: auto;
  max-width: 362px;
  margin: 20px auto;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 0 60px rgba($color-black,.25);
  &--register {
    background-image: url('../img/popup-background.png');
    background-position: left bottom;
    background-repeat: no-repeat; }
  &__title {
    font-family: $font-intro;
    color: $primary-color;
    text-align: center;
    font-size: rem(25);
    margin-bottom: 20px; }
  &__close {
    position: absolute;
    right: 17px;
    top: 0px;
    font-size: rem(35);
    color: #bdbdbd;
    line-height: 1;
    cursor: pointer;
    transition: color 0.35s;
    &:hover {
      color: $primary-color; } } }

.mfp-bg {
  background: rgba($color-white,.8); }

