// Basic Styles
html {
  font-size: inherit;
  line-height: 1.3;
  font-size: 16px; }

html,
body {
  height: 100%;
  padding: 0;
  margin: 0;
  min-width: 320px; }

.base {
  transform: translateX(0);
  transition: transform 0.35s;
  height: 100%;
  width: 100%; }

body {
  background: $color-white;
  font-size: rem(16);
  font-family: $font-roboto-light;
  color: $primary-color;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
  &.is-active {
    position: fixed;
    .base {
      will-change: transform;
      transform: translateX(-166px); } } }

//  Typography
.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
  color: $primary-color;
  font-weight: normal;
  font-family: $font-roboto;
  text-decoration: none;
  margin: 0 0 26px 0; }

.h1, h1 {
  font-size: rem(50);
  line-height: 1;
  letter-spacing: -1px;
  font-family: $font-intro; }

.h2, h2 {
  font-size: rem(30);
  line-height: 1.14;
  font-family: $font-intro; }

.h3, h3 {
  font-size: rem(25);
  line-height: 1.38;
  &--intro {
    font-family: $font-intro; } }

.h4, h4 {
  font-size: rem(21);
  line-height: 1.38;
  margin-bottom: 20px; }

.h5, h5 {
  font-size: rem(18);
  margin-bottom: 20px; }

.h6, h6 {
  font-size: rem(16);
  margin-bottom: 20px; }

.small, small {
  font-size: rem(12);
  line-height: 1.2; }

p {
  line-height: 1.6;
  margin: 0 0 28px 0; }

em, i, .em, .i {
  font-style: italic; }

strong, b, .strong, .b {
  font-weight: 400;
  font-family: $font-roboto-medium; }

hr {
  border: solid #ddd;
  border-width: 1px 0 0;
  clear: both;
  margin: 10px 0 30px;
  height: 0; }

// Links
a, .link {
  color: $link-color;
  text-decoration: underline;
  outline: none;
  transition: color 0.35s, background-color 0.35s, border-color 0.35s;

  &:hover, &:focus, &:active {
    color: $link-color;
    text-decoration: none;
    outline: none; } }

a.link {
  &:hover, &:focus, &:active {
    text-decoration: none; } }

.container {
  margin: 0 auto;
  // width: 1280px
  width: 100%;
  min-width: 320px;
  padding: 0 15px; }

.content {
  overflow: hidden;
  padding-top: 20px;
  .section {
    min-width: 100%; } }

/* text align */
.text {
  &-center, &--center {
    text-align: center; }
  &-left, &--left {
    text-align: left; }
  &-right, &--right {
    text-align: right; } }

/* loader */
.loader {
  width: 100%;
  height: 100vh;
  left: 0;
  top: 0;
  position: fixed;
  z-index: 10000;
  background-color: $color-white;
  &__block {
    background: $color-darkorange;
    animation: load1 1s infinite ease-in-out;
    width: 1em;
    height: 4em;
    position: absolute;
    top: 50%;
    left: 50%;
    color: $color-darkorange;
    text-indent: -9999em;
    font-size: 11px;
    animation-delay: -0.16s;
    transition: transform(-50%,-50%);
    &:before, &:after {
      background: $color-darkorange;
      animation: load1 1s infinite ease-in-out;
      width: 1em;
      height: 4em; }
    &:before, &:after {
      position: absolute;
      top: 0;
      content: ''; }
    &:before {
      left: -1.5em;
      animation-delay: -0.32s; }
    &:after {
      left: 1.5em; } }
  @-webkit-keyframes load1 {
    0%,
    80%,
    100% {
      box-shadow: 0 0;
      height: 4em; }
    40% {
      box-shadow: 0 -2em;
      height: 5em; } }
  @keyframes load1 {
    0%,
    80%,
    100% {
      box-shadow: 0 0;
      height: 4em; }
    40% {
      box-shadow: 0 -2em;
      height: 5em; } } }
@-webkit-keyframes load7 {
  0%,
  80%,
  100% {
    box-shadow: 0 2.5em 0 -1.3em lightskyblue; }
  40% {
    box-shadow: 0 2.5em 0 0 lightskyblue; } }
@keyframes load7 {
  0%,
  80%,
  100% {
    box-shadow: 0 2.5em 0 -1.3em lightskyblue; }
  40% {
    box-shadow: 0 2.5em 0 0 lightskyblue; } }

/* img */
.img {
  &-responsive, &--responsive {
    display: block;
    height: auto;
    max-width: 100%; }

  &-round, &--round {
    border-radius: 50%; } }

/* section */
.section {
  padding: 50px 0;
  min-width: 320px;
  &__head {
    text-align: center;
    &--big {
      .h1, h1 {
        line-height: 1.2;
        font-size: rem(70);
        &>* {
          vertical-align: middle; }
        b {
          background: $color-blue;
          transform: skewX(-10deg);
          color: $color-white;
          font-family: $font-intro;
          padding: 0 20px;
          justify-content: center;
          align-items: center;
          margin-left: 15px;
          border-radius: 10px;
          line-height: 1;
          vertical-align: middle;
          font-family: $font-roboto-bold;
          // font-weight: bold
          text-transform: uppercase;
          display: inline-block;
          span {
            transform: skewX(10deg);
            display: inline-block;
            line-height: 1;
            // padding: 10px 0
            height: 90px;
            line-height: 90px; } } } } } }

ul li {
  list-style-type: disc; }
ol li {
  list-style-type: decimal; }

/* bg */
.bg {
  &--pink {
    background-color: pink !important; }
  &--white {
    background-color: $color-white !important; }
  &--black {
    background-color: $color-black !important; } }

/* no padding no margin */
.no {
  &--padding {
    padding: 0 !important;
    &-top {
      padding-top: 0 !important; }
    &-bottom {
      padding-bottom: 0 !important; }
    &-left {
      padding-left: 0 !important; }
    &-right {
      padding-right: 0 !important; } }

  &--margin {
    margin: 0 !important;
    &-top {
      margin-top: 0 !important; }
    &-bottom {
      margin-bottom: 0 !important; }
    &-left {
      margin-left: 0 !important; }
    &-right {
      margin-right: 0 !important; } } }

/* icons */
.icon {
  display: inline-block;
  // &--sprite
 }  //   @include sprite($img-name)

/* color */
.color {
  &--black {
    color: $color-black !important; }
  &--white {
    color: $color-white !important; } }
